import React from "react";
import { Select, SelectItem } from "@nextui-org/react";

type elemento = {
  key: number;
  label: string;
};

interface DesplegableProps {
  titulo?: string;
  explicacion?: string;
  elementos: elemento[];
  onSelect?: (key: number) => void;
}

export default function Desplegable(props: DesplegableProps) {
  const { titulo, explicacion, elementos, onSelect } = props;

  return (
    <div>
      <Select
        size="md"
        label={explicacion}
        placeholder={titulo}
        className="max-w-xs min-w-300"
      >
        {elementos.map((elemento) => (
          <SelectItem
            onClick={() => onSelect?.(elemento.key)}
            key={elemento.key}
          >
            {elemento.label}
          </SelectItem>
        ))}
      </Select>
    </div>
  );
}
